
import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import imgArriba from './Arriba.png';
import imgAbajo from './Abajo.png';
import imgIgual from './Igual.png';

import {todos}  from './Todos.json';
import {seleccionados}  from './Seleccionados.json';

import TodoForm from './Components/TodoForm';
import DetalleForm from './Components/DetalleForm';

//console.log(todos);

class App extends Component {
  constructor(){
  	super();
  	this.state = {
  		title: 'Aplicacion de Tareas',
  		ntareas: 10,
      ResaltarIndicadorId: 0,
      verEquilibrioInterno: 'none',
      todos,
      seleccionados
  	};
    this.handleAddTodo =  this.handleAddTodo.bind(this);
    this.handleAddSeleccionado =  this.handleAddSeleccionado.bind(this);
  }
  handleAddTodo(todo){
  	this.setState({
  		todos: [...this.state.todos,todo]
  	})
  }

  handleAddSeleccionado(seleccionado){
  	this.setState({
  		seleccionados: [...this.state.seleccionados,seleccionado]
  	})
  }

  removeTodo(index){
    //console.log(index);
    this.setState({
      seleccionados: this.state.seleccionados.filter((e,i) => {
        return i !== index
      })
    })
  }

  AccionResaltarIndicador(xIndicadorID){
    //console.log(xIndicadorID);
    this.setState({
      ResaltarIndicadorId: xIndicadorID
    });
  }


  MostrarDetalle(ObjDetalle){
    return (<pre align="left">Prueba</pre>);
  }

  ImagenAccion(valor){
      //console.log('ImagenAccion')
      //console.log(valor)
      if (valor>0){
        return (imgArriba)
      }
      if (valor<0){
        return (imgAbajo)
      }
      if (valor==0){
        return (imgIgual)
      }
  }


  render() {
    console.log('Antes de renderizar el componente')
    const seleccionados = this.state.seleccionados.map((todo,i)=> {
    	return (
        <div className="col-md-4" key={i}>
          <div className="card mt-4">
            <div className="card-header">
              <h4>{todo.Entrada_Accion}</h4>
              <h6>Movimiento de Capitales: {todo.Entrada_MovimientoDeCapitales}</h6>
              <h6>Tipo de Cambio: {todo.Entrada_TipoDeCambio}</h6>
              <h6>Esquema: {todo.Entrada_Esquema}</h6>
              <span className="badge badge-pill badge-danger ml-2">
                {todo.priority}
              </span>
            </div>
            <div className="card-body">
              <pre align="left"><DetalleForm Detalles={todo.DetalleEI} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,2)} >Demanda Agregada</button> <img src={this.ImagenAccion(todo.Salida_EI_DemandaAgregada)}/> {todo.Salida_EI_DemandaAgregada}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,10)} >Tasa de interes</button> <img src={this.ImagenAccion(todo.Salida_EI_TasaDeInteres)}/>  {todo.Salida_EI_TasaDeInteres}
              <br></br><br></br>
              <pre align="left"><DetalleForm Detalles={todo.DetalleBP} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,14)} >Balanza Comercial</button> <img src={this.ImagenAccion(todo.Salida_BP_BalanzaComercial)}/> {todo.Salida_BP_BalanzaComercial} <br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,22)} >Balanza Capital y Financiera</button> <img src={this.ImagenAccion(todo.Salida_BP_BalanzaCapitalyFinanciera)}/> {todo.Salida_BP_BalanzaCapitalyFinanciera} <br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,23)} >Balanza De Pagos</button> <img src={this.ImagenAccion(todo.Salida_BP_BalanzaDePagos)}/> {todo.Salida_BP_BalanzaDePagos}

              <pre align="left"><DetalleForm Detalles={todo.DetalleEE} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,2)} >Demanda Agregada</button> <img src={this.ImagenAccion(todo.Salida_EE_DemandaAgregada)}/> {todo.Salida_EE_DemandaAgregada} <br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,10)} >Tasa de interes</button> <img src={this.ImagenAccion(todo.Salida_EE_TasaDeInteres)}/>  {todo.Salida_EE_TasaDeInteres}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,11)} >Inversion</button> <img src={this.ImagenAccion(todo.Salida_EE_Inversion)}/>  {todo.Salida_EE_Inversion}<br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,13)} >Imp.</button> <img src={this.ImagenAccion(todo.Salida_EE_Importaciones)}/>  {todo.Salida_EE_Importaciones}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,26)} >Exp.</button> <img src={this.ImagenAccion(todo.Salida_EE_Exportaciones)}/>  {todo.Salida_EE_Exportaciones}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,25)} >Exp. Netas</button> <img src={this.ImagenAccion(todo.Salida_EE_ExportacionesNetas)}/>  {todo.Salida_EE_ExportacionesNetas}<br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,20)} >Reservas BC</button> <img src={this.ImagenAccion(todo.Salida_BC_Reservas)}/>  {todo.Salida_BC_Reservas}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,12)} >Oferta Monetaria</button> <img src={this.ImagenAccion(todo.Salida_BC_OfertaMonetaria)}/>  {todo.Salida_BC_OfertaMonetaria}

              <pre align="left"><DetalleForm Detalles={todo.DetallePE} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,27)} >Precio</button> <img src={this.ImagenAccion(todo.Salida_PE_Precio)}/> {todo.Salida_PE_Precio} <br></br>
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,30)} >Produccion</button> <img src={this.ImagenAccion(todo.Salida_PE_Produccion)}/> {todo.Salida_PE_Produccion}
              <button className="btn btn-link"  onClick = {this.AccionResaltarIndicador.bind(this,29)} >Desempleo</button> <img src={this.ImagenAccion(todo.Salida_PE_Desempleo)}/> {todo.Salida_PE_Desempleo}
            </div>
            <div className="card-footer">
              <button
                className="btn btn-danger"
                onClick = {this.removeTodo.bind(this,i)}
              >
                Delete
              </button>
            </div>
      		</div>
        </div>
    	)
    })

    return (
      <div className="App">
        <nav className="navbar navbar-dark bg-dark">
            <a href="www.googel.com" className="text-white">
              {this.state.title} -
              <span className="badge badge-pill badge-light ml-2">
                {this.state.seleccionados.length}
              </span>

            </a>
        </nav>
          <div className="row mt-1 ml-1 mr-1">
            <div className="col md-5 mr-5">
              <img src={logo} className="App-logo" alt="logo" />
              <TodoForm todos={this.state.todos} onAddSeleccionado={this.handleAddSeleccionado} />
            </div>
            </div>
          <div className="row mt-1 ml-1 mr-1">
            <div className="col md-1" colSpan="3">
              <div className="row">
                {seleccionados}
              </div>
            </div>
          </div>

      </div>
    );
  }
}

export default App;
